___CSS_LOADER_EXPORT___.push([module.id, `

body {
    height: 100vh;
    font-family: Helvetica;
    margin: 0 40px;
    box-sizing: border-box;
}

.content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: 1rem;
    margin: 40px 0;
}

.todo {
    display: grid;
    border-radius: 10px;
    margin: 5px;
    grid-template-columns: 1fr 4fr 3fr 2fr 2fr;
    font-size: 16px;
    padding: 10px;
    gap: 5px;
    background-color: #ffffff;
}

.todotitle {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    font-size: 18px;
    font-weight: bold;
}

.todo div:not(.todotitle) {
    grid-row: 2 / 3;
}

.todo input, .todo button{
    grid-row: 1 / 3;
}

.todo[data-priority="low"] {
    background-color: #7bf1a7;
}

.todo[data-priority="med"] {
    background-color: #fef186;
}

.todo[data-priority="high"] {
    background-color: #ffa2a3;
}

.todo:has(input:checked) div {
    text-decoration: line-through;
}

.project {
    background-color: #d6e7f5;
    padding: 10px;
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    display: flex;
    flex-direction: column;
}

.button-container {
    margin-top: auto;
}

button {
    background-color: #4591d3;
    border: none;
    color: #ffffff;
    border-radius: 10px;
    padding: 10px;
}

button:hover {
    background-color: #6ba3d4;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":";;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,cAAc;IACd,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,4DAA4D;IAC5D,SAAS;IACT,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,0CAA0C;IAC1C,eAAe;IACf,aAAa;IACb,QAAQ;IACR,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,iDAAiD;IACjD,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,cAAc;IACd,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["\n\nbody {\n    height: 100vh;\n    font-family: Helvetica;\n    margin: 0 40px;\n    box-sizing: border-box;\n}\n\n.content {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));\n    gap: 1rem;\n    margin: 40px 0;\n}\n\n.todo {\n    display: grid;\n    border-radius: 10px;\n    margin: 5px;\n    grid-template-columns: 1fr 4fr 3fr 2fr 2fr;\n    font-size: 16px;\n    padding: 10px;\n    gap: 5px;\n    background-color: #ffffff;\n}\n\n.todotitle {\n    grid-column: 2 / 3;\n    grid-row: 1 / 2;\n    font-size: 18px;\n    font-weight: bold;\n}\n\n.todo div:not(.todotitle) {\n    grid-row: 2 / 3;\n}\n\n.todo input, .todo button{\n    grid-row: 1 / 3;\n}\n\n.todo[data-priority=\"low\"] {\n    background-color: #7bf1a7;\n}\n\n.todo[data-priority=\"med\"] {\n    background-color: #fef186;\n}\n\n.todo[data-priority=\"high\"] {\n    background-color: #ffa2a3;\n}\n\n.todo:has(input:checked) div {\n    text-decoration: line-through;\n}\n\n.project {\n    background-color: #d6e7f5;\n    padding: 10px;\n    border-radius: 10px;\n    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;\n    display: flex;\n    flex-direction: column;\n}\n\n.button-container {\n    margin-top: auto;\n}\n\nbutton {\n    background-color: #4591d3;\n    border: none;\n